//大作业

import React from 'react';
import {BrowserRouter as Router, Redirect, Route} from 'react-router-dom';
import Home from './shop/Home';
import Collection from './shop/Collection';
import SearchFor from './shop/SearchFor';
import Detail from './shop/Detail';
import Login from './shop/Login';
const App = () => {
    return (
        <div>
            <Router>
                <Route exact path='/react5/build' component={Home} />
                <Route path='/collection' component={Collection} />
                <Route path='/home/search' component={SearchFor} />
                <Route path='/home/detail/:id' component={Detail} />
                <Route path='/login' component={Login} />
                <Route render={ () => <Redirect to='/react5/build' /> } />
            </Router>
        </div>
    )
}
export default App;



