import React from 'react'
import { SearchBar } from 'antd-mobile';
import CarouselLower from './CarouselLower';
import Mytab from './Mytab';
import store from './reduxShop/store';
class Home extends React.Component {
    constructor(){
        super();
        this.state = store.getState();
        this.getList = this.getList.bind(this);
        store.subscribe(this.getList)
    }
    componentDidMount = () => {
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
            .then((res) => res.json())
            .then((myJson) => {
                console.log(myJson);
                let values = myJson.data;
                const action = {
                    type:'get_datalist',
                    list:values
                }
                store.dispatch(action);
            });
    }
    getList(){
        this.setState(store.getState());
    }
    getImage(idx){
        let arr = this.state.datalist;
        for (var i = idx; i < arr.length; i++) {
            let id = store.getState().datalist[i].id;
            let id1 = store.getState().datalist[i+1].id;
            return (
                <div>
                    <div style={{float:'left',marginLeft:'15px'}}
                            onClick={() => {
                                this.props.history.push(`/home/detail/${id}`)
                            }}
                    >
                        <img style={{ width: '170px', height: '170px' }}
                                src={arr[i].img} ></img>
                        <p style={{color:'#ff5500'}}>￥{arr[i].price}</p>
                        <p style={{color:'#999'}}>{arr[i].title}</p>
                    </div>
                    <div style={{float:'left',marginLeft:'6px'}}
                            onClick={() => this.props.history.push(`/home/detail/${id1}`)}>
                        <img style={{ width: '170px', height: '170px' }}
                                src={arr[i + 1].img}></img>
                        <p style={{color:'#ff5500'}}>￥{arr[i + 1].price}</p>
                        <p style={{color:'#999'}}>{arr[i + 1].title}</p>
                    </div>
                </div>
            )
        }
    }
    render(){
        return (
            <div>
                {/* 搜索框 */}
                <SearchBar placeholder="请输入你要搜索的内容"
                        onFocus={() => this.props.history.push('/home/search')}/>
                {/* 轮播图 */}
                <CarouselLower></CarouselLower>
                {/* 商品列表 */}
                <div>
                    {this.getImage(0)}
                    {this.getImage(2)}
                    {this.getImage(4)}
                    {this.getImage(6)}
                    {this.getImage(8)}
                </div>
                <Mytab />
            </div>
        )
    }
}
export default Home;