const defaultState = {
    datalist:[],
    shoucang:[],
    userinfo:'',
};

export default (state = defaultState,action) => {
    if(action.type === 'get_datalist'){
        const newState = JSON.parse(JSON.stringify(state));
        newState.datalist = action.list;
        return newState;
    }
    if(action.type === 'push_collection'){
        const newState = JSON.parse(JSON.stringify(state));
        newState.shoucang.push(action.collection_list);
        return newState;
    }
    if(action.type === 'delete_collection'){
        const newState = JSON.parse(JSON.stringify(state));
        newState.shoucang.splice(action.idx,1);
        return newState;
    }
    return state;
}