import React from 'react'
import Mytab from '../shop/Mytab'
import store from './reduxShop/store';
import { Button } from 'antd-mobile';
const Collection = (props) => {
    const list = store.getState().shoucang;
    const getColList = (idx) => {
        var arr = list;
        for(var i=idx;i<arr.length;i++){
            return (
                <div style={{width:'100%',height:'200px',border:'#ccc 1px solid'}}>
                    <img src={arr[i].img} style={{width:'50%',height:'80%',float:'left'}}></img>
                    <p style={{float:'left',
                            color:'#ff5500',fontSize:'20px',marginLeft:'30px'}}
                        >￥{arr[i].price}</p>
                    <p style={{float:'left',color:'#000',fontSize:'20px',
                            marginTop:'10px',marginLeft:'10px'}}
                        >{arr[i].title}</p>
                    <div style={{width:'40%',height:'10%',float:'right'}}>
                        <Button style={{width:'70%',backgroundColor:'#ffc101',color:'#fff'}}
                            onClick={() => {
                                const action = {
                                    type:'delete_collection',
                                    idx:i
                                }
                                store.dispatch(action);
                                props.history.push('/collection');
                            }}
                        >取消收藏</Button>
                    </div>
                </div>
            )
        }
    }
    return(
        <div>
            {getColList(0)}
            {getColList(1)}
            {getColList(2)}
            {getColList(3)}
            {getColList(4)}
            {getColList(5)}
            {getColList(6)}
            {getColList(7)}
            {getColList(8)}
            {getColList(9)}
            <Mytab />
        </div>
    )
}
export default Collection;