import React, {Component} from 'react'
import { Carousel, WingBlank } from 'antd-mobile';

class CarouselLower extends Component {
  state = {
    data: ['gw.alicdn.com/imgextra/i1/68/O1CN01BQL7ux1CNBZ0UTqaw_!!68-0-lubanu',
             'aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50', 
             'aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpghttps://gw.alicdn.com/imgextra/i2/75/O1CN01co3LS91CQOKyY1XMc_!!75-0-lubanu',
             'gw.alicdn.com/imgextra/i4/81/O1CN01LbIJTv1CT8iDY67wG_!!81-0-lubanu',
             'img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i1/6000000001004/O1CN017bePXo1JHsHSvNh8V_!!6000000001004-0-octopus'
          ],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['gw.alicdn.com/imgextra/i1/68/O1CN01BQL7ux1CNBZ0UTqaw_!!68-0-lubanu',
        'aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50', 
        'aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpghttps://gw.alicdn.com/imgextra/i2/75/O1CN01co3LS91CQOKyY1XMc_!!75-0-lubanu',
        'gw.alicdn.com/imgextra/i4/81/O1CN01LbIJTv1CT8iDY67wG_!!81-0-lubanu',
        'img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i1/6000000001004/O1CN017bePXo1JHsHSvNh8V_!!6000000001004-0-octopus'
      ],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite={true}
        >
          {this.state.data.map(val => (
            <a href="/"
              key={val}
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`https://${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default CarouselLower;