import React from 'react'
import { Button } from 'antd-mobile';
const Login = (props) => {
    const login = () => {
        localStorage.setItem('userinfo','json')
        props.history.replace(props.location.state1)
    }
    return (
        <div>
            <form>
                <input placeholder='账号' type='text' required
                    style={{display:'block',border:'0',fontSize:'16px',color:'#333',paddingLeft:'25px',
                        width:'80%',height:'30px',margin:'0 auto',marginTop:'120px'}}
                />
                <br/>
                <input placeholder='密码' type='password' required
                    style={{display:'block',border:'0',fontSize:'16px',color:'#333',paddingLeft:'25px',
                    width:'80%',height:'30px',margin:'0 auto',marginTop:'20px'}}
                />
                <div style={{marginTop:'30px'}}>
                    <Button style={{background:'-webkit-linear-gradient(left,#ff9000,#ff5000) no-repeat', 
                        color:'#fff',borderShadow:'0 2px 4px #f7c7b1',borderRadius:'35px',
                        width:'90%',height:'45px',margin:'0 auto',fontSize:'16px'}}
                        onClick={ login }
                    >登录</Button>
                </div>
            </form>
        </div>
    )
}

export default Login;