import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter, Link } from 'react-router-dom';
import store from './reduxShop/store';

class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'redTab',
        };
    }
    
    render() {
        const { pathname } = this.props.location;

        let id = pathname.substring(13,16);
        let datalist = store.getState().datalist;
        let colList = datalist[id-100];
        console.log(colList);
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu1'></i>                           
                        }
                        selected={ pathname === '/react5/build' }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-kefu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu1'></i>                           
                        }
                        title="客服"
                        key="kefu"
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang2'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang3'></i>                           
                        }
                        title="收藏"
                        key="shoucang"
                        selected={this.state.selectedTab === 'click'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'click',
                            });
                            if(!localStorage.getItem('userinfo')){
                                this.props.history.push({
                                    pathname:'/login',
                                    state1:this.props.location
                                })
                            }else{
                                const action = {
                                    type:'push_collection',
                                    collection_list:colList
                                };
                                store.dispatch(action);
                            }
                        }}
                        data-seed="logId1"
                    >
			        </TabBar.Item>
                    <div title="加入购物车">
                    </div>
                    <div title="立即购买">
                    </div>
                </TabBar>
                
            </div>
        );
    }
}
export default withRouter(Tab);