import React from 'react'
import { SearchBar } from 'antd-mobile';
const SearchFor = (props) => {
    return (<div>
        <SearchBar placeholder="请输入你要搜索的内容" 
          // 点取消，返回首页 
          showCancelButton
          onCancel={props.history.goBack}
        />
    </div>);
}

export default SearchFor;